import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, } from '@angular/router';
import { Somatotipo } from '../shared/somatotipo';
import { DataService } from '../data.service';
@Component({
  selector: 'app-somatotipo',
  templateUrl: './somatotipo.component.html',
  styleUrls: ['./somatotipo.component.css']
})
export class SomatotipoComponent implements OnInit {
  habitosForm: FormGroup;
  model: Somatotipo;
  submittedModel: Somatotipo;
  submitted: boolean =  false;
  tobillos: object;
  grasa: object;
  ejercicio: object;
  cuerpo: object;
  asimila: object;
  fluctua: object;
  frecuencia: object;
  estilo: object;
  sociable: object;
  interes: object;
  constructor(private formBuilder: FormBuilder, private data: DataService, public router: Router) {
    this.habitosForm = this.createMyForm();
  }

  ngOnInit() {
    this.tobillos = [{ 'text': 'Grandes.', 'value': 'A' },
    { 'text': 'Medianos.', 'value': 'B' },
    { 'text': 'Pequeños.', 'value': 'C' }];


    this.grasa = [{ 'text': 'Mucha.', 'value': 'A' },
    { 'text': 'Regular/poca.', 'value': 'B' },
    { 'text': 'Muy poca.', 'value': 'C' }];

    this.ejercicio = [{ 'text': 'Si y tambien grasa corporal.', 'value': 'A' },
    { 'text': 'Si y con facilidad.', 'value': 'B' },
    { 'text': 'Poca y con dificultad.', 'value': 'C' }];

    this.cuerpo = [{ 'text': 'Redondeado / de aparencia flácida.', 'value': 'A' },
    { 'text': 'Atlético / fuerte.', 'value': 'B' },
    { 'text': 'Delgada / frágil.', 'value': 'C' }];

    this.asimila = [{ 'text': 'Excesiva.', 'value': 'A' },
    { 'text': 'Normal.', 'value': 'B' },
    { 'text': 'Escasa.', 'value': 'C' }];

    this.fluctua = [{ 'text': 'Aumento de peso muy fácilmente y bajo con dificultad.', 'value': 'A' },
    { 'text': 'Gano peso fácilmente, pero lo pierdo rápido.', 'value': 'B' },
    { 'text': 'Pierdo peso muy fácilmente.', 'value': 'C' }];

    this.frecuencia = [{ 'text': 'Mi apetito es muy fuerte y en todo momento.', 'value': 'A' },
    { 'text': 'Mi apetito es normal y a las horas habituales de comer.', 'value': 'B' },
    { 'text': 'Mi apetito es débil y pasa facilmente.', 'value': 'C' }];

    this.estilo = [{ 'text': 'Siempre Tranquilo y sedentario.', 'value': 'A' },
    { 'text': 'Siempre Activo y en movimiento.', 'value': 'B' },
    { 'text': 'Siempre hiperactivo física y mentalmente.', 'value': 'C' }]

    this.sociable = [{ 'text': 'Alta, con preferencia por estar en grupo.', 'value': 'A' },
    { 'text': 'Regular o normal, es indiferente.', 'value': 'B' },
    { 'text': 'Baja, tendencia al aislamiento.', 'value': 'C' }];

    this.interes = [{ 'text': 'Alto, la comida es prioridad.', 'value': 'A' },
    { 'text': 'Normal o promedio.', 'value': 'B' },
    { 'text': 'Bajo, no importa saltarse comidas.', 'value': 'C' }];

  }
  createMyForm() {
    return this.formBuilder.group({
      tobillos: ['', Validators.compose([Validators.required])],
      grasa: ['', Validators.compose([Validators.required])],
      ejercicio: ['', Validators.compose([Validators.required])],
      cuerpo: ['', Validators.compose([Validators.required])],
      asimila: ['', Validators.compose([Validators.required])],
      fluctua: ['', Validators.compose([Validators.required])],
      frecuencia: ['', Validators.compose([Validators.required])],
      estilo: ['', Validators.compose([Validators.required])],
      sociable: ['', Validators.compose([Validators.required])],
      interes: ['', Validators.compose([Validators.required])]


    });
  }

  onSubmit({ value, valid }: { value: Somatotipo, valid: boolean }) {
    this.submitted = true;
    this.submittedModel = value;
    this.data.somatotipo = this.submittedModel;;


    this.router.navigate(['final']);
  }
}
