import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AcumulacionFormComponent } from './acumulacion/acumulacionForm.component';
import { BienvenidaComponent } from './bienvenida/bienvenida.component';
import { MetabolicoComponent } from './metabolico/metabolico.component';
import { SomatotipoComponent } from './somatotipo/somatotipo.component';
import { FinalComponent } from './final/final.component';


const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: '/bienvenida' },
  { path: 'acumulacion',     component: AcumulacionFormComponent  },
  { path: 'bienvenida',     component: BienvenidaComponent  },
  { path: 'metabolico',     component: MetabolicoComponent  },
  { path: 'somatotipo',     component: SomatotipoComponent  },
  { path: 'final',     component: FinalComponent  }];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {
  static components = [  AcumulacionFormComponent ];
}


