import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Acumulacion } from '../shared/acumulacion';
import { DataService } from '../data.service';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'acumulacion-driven-form',
  templateUrl: './acumulacionForm.component.html'
})
export class AcumulacionFormComponent implements OnInit {
  heroForm: FormGroup;
  model: Acumulacion;
  submittedModel: Acumulacion;
  acumula: object;
  antojos: object;
  dolor: object;
  energia: object;
  preocupa: object;
  dormir: object;
  dia: object;
  perder_peso: object;
  submitted = false;
  constructor(private formBuilder: FormBuilder, private data: DataService, public router: Router) {
    this.heroForm = this.createMyForm();
   }

  ngOnInit() {

    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0);
  });

    this.acumula = [{'text': 'Acumulo en el abdómen y se ve caído y/o flacido.','value':'A'},
    {'text':'Acumulo entre los hombros y las rodillas de forma proporcional.','value':'B'},
    {'text':'Acumulo en el abdómen y se vé inflado y/o tenso.','value':'C'},
    {'text':'Acumulo en las caderas y muslos y sobresale piel de naranja.','value':'D'}];

    this.antojos = [{'text':'Piqueos, papas fritas, maní salado, chocolate, cheesecake.','value':'A'},
    {'text':'Panes, pastas, harinas (Arroz, Trigo, Maiz, Soja).','value':'B'},
    {'text':'Frituras, empanizado, apanado, mayonesa, aceites.','value':'C'},
    {'text':'Helados, muffins, tartas o postres.','value':'D'}];

    this.dolor = [{'text':'Las manos o muñecas.','value':'A'},
    {'text':'Las rodillas, o codos.','value':'B'},
    {'text':'El hombro derecho o el cuello.','value':'C'},
    {'text':'La zona pélvica, sufro de cólicos menstruales severos.','value':'D'}];


    this.energia = [{'text':'Por las mañanas y medio adormilado(a) por la tarde.','value':'A'},
    {'text':'Tengo picos y caídas de energía durante el día.','value':'B'}];


    this.preocupa = [{'text':'Solo pienso en ese tema y no puedo sacarlo de mi mente.','value':'A'},
    {'text':'Me vuelvo olvidadizo(a) y distraído(a).','value':'B'},
    {'text':'Adopto una actitud sensible o emotivo(a). ','value':'D'}];

    this.dormir = [{'text':'Me demoro en conciliar el sueño.','value':'A'},
    {'text':'Me demoro en poder levantarme.','value':'B'},
    {'text':'Duermo bien, pero me levanto con la sensación de seguir cansado(a).','value':'C'}];

    this.dia = [{'text':'Retención de líquidos y/o caída de cabello.','value':'A'},
    {'text':'Uñas quebradizas, y/o pies fríos. ','value':'B'},
    {'text':'Reflujo y/o embalonamiento del abdomen.','value':'C'}];

    this.perder_peso = [{'text':'Estoy agotado(a) y no puedo ejercitarme bien.','value':'A'},
    {'text':'Tengo antojos incontrolables. ','value':'B'},
    {'text':'Si no obtengo resultados rápido, renuncio.','value':'C'},
    {'text':'Soy flojo(a) y prefiero relajarme','value':'D'}];


  }
  createMyForm() {
    return this.formBuilder.group({
      acumula: ['', Validators.compose([ Validators.required])],
      antojos: ['', Validators.compose([ Validators.required])],
      dolor: ['', Validators.compose([ Validators.required])],
      energia: ['', Validators.compose([  Validators.required])],
      preocupa: ['', Validators.compose([  Validators.required])],
      dormir: ['', Validators.compose([  Validators.required])],
      dia: ['', Validators.compose([  Validators.required])],
      perder_peso: ['', Validators.compose([  Validators.required])]
    });
  }

  onSubmit({ value, valid }: { value: Acumulacion, valid: boolean }) {
    this.submitted = true;
    this.submittedModel = value;
    this.data.acumulacion = this.submittedModel;
    this.router.navigate(['metabolico']);
 /*   const header = new HttpHeaders({'Content-Type': 'application/json'});

  console.log(JSON.stringify(value));
    const req = this.http.post('https://capi-hernan.herokuapp.com/aliados',JSON.stringify(value),{headers: header})
      .subscribe(
        res => {
          console.log(res);
        },
        err => {
          console.log('Error al conectar al servicio');
        }
      );
  }*/
}
}
