import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Metabolico } from '../shared/metabolico';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-metabolico',
  templateUrl: './metabolico.component.html',
  styleUrls: ['./metabolico.component.css']
})
export class MetabolicoComponent implements OnInit {
  metaForm: FormGroup;
  model: Metabolico;
  submittedModel: Metabolico;
  submitted: boolean;
  apetito: object;
  sueno: object;
  digestion: object;
  episodio: object;
  ojos: object;
  saliva: object;
  estres: object;
  alimento: object;
  snack: object;
  tolerancia: object;
  constructor(private formBuilder: FormBuilder, private data: DataService, public router: Router) {
    this.metaForm = this.createMyForm();
  }

  ngOnInit() {
    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      window.scrollTo(0, 0);
    });

    this.apetito = [{ 'text': 'Fuerte y necesito comer.', 'value': 'A' },
    { 'text': 'Muy débil, desaparece rápidamente.', 'value': 'B' },
    { 'text': 'Moderado, necesito comer un snack (Sandwich, Hamburguesa, entre otros).', 'value': 'C' },
    { 'text': 'Débil, pasa con un pequeño snack (Galletas,Fruta entre otros).', 'value': 'D' }];

    this.sueno = [{ 'text': 'Mala, demoro en conciliar el sueño y me despierto fácilmente.', 'value': 'A' },
    { 'text': 'Muy buena, duermo profundamente con facilidad.', 'value': 'B' },
    { 'text': 'Regular, demoro un poco en dormir y me despierto por ratos.', 'value': 'C' },
    { 'text': 'Buena pero tardo un poco en conciliar el sueño. ', 'value': 'D' }];

    this.digestion = [{ 'text': 'Fuerte, no suelo tener problemas digestivos', 'value': 'A' },
    { 'text': 'Muy débil, suelo padecer de colón perezoso.', 'value': 'B' },
    { 'text': 'Fuerte pero se debilita ante estrés prolongado.', 'value': 'C' },
    { 'text': 'Débil durante periodos de estrés. ', 'value': 'D' }];

    this.episodio = [{ 'text': 'Muy rara vez.', 'value': 'A' },
    { 'text': 'Muy frecuentemente.', 'value': 'B' },
    { 'text': 'Sólo durante periodos de estrés prolongado.', 'value': 'C' },
    { 'text': 'Ocacionalmete, durante periodos de estrés o ansiedad.', 'value': 'D' }];

    this.ojos = [{ 'text': 'Siempre humedos.', 'value': 'A' },
    { 'text': 'Se resecan y suelo requerir lágrimas artificiales.', 'value': 'B' },
    { 'text': 'Regular en todo momento.', 'value': 'C' },
    { 'text': 'Resecos durante periodos de estrés.', 'value': 'D' }];

    this.saliva = [{ 'text': 'Escasa; suelo padecer de boca seca.', 'value': 'A' },
    { 'text': 'Abundante; siempre salivo con mucha facilidad.', 'value': 'B' },
    { 'text': 'Usualmente Normal;  pero seca por momentos.', 'value': 'C' },
    { 'text': 'Normal siempre.', 'value': 'D' }];

    this.estres = [{ 'text': 'Casi nula o nula.', 'value': 'A' },
    { 'text': 'Muy fácilmente.', 'value': 'B' },
    { 'text': 'Sólo si el estrés es prolongado e intenso.', 'value': 'C' },
    { 'text': 'Fácilmente si el estrés es intenso.', 'value': 'D' }];

    this.alimento = [{ 'text': 'Parrilla, BBQ,  vísceras, pollo a la braza y embutidos.', 'value': 'A' },
    { 'text': 'Pastas, cereales,  lácteos, quesos blancos y frutas.', 'value': 'B' },
    { 'text': 'Pollo, pavo, animal de corral, salmón, trucha, vegetales, cítricos.', 'value': 'C' },
    { 'text': 'Pescado blanco, pechuga de pollo, menestras, tuberculos.', 'value': 'D' }];

    this.snack = [{ 'text': 'Pop corn, papas fritas, embutidos, doritos.', 'value': 'A' },
    { 'text': 'Helado, chocolate, pastelillos, cupcake.', 'value': 'B' },
    { 'text': 'Cualquiera, pero prefiero más salados que dulces.', 'value': 'C' },
    { 'text': 'Cualquiera, pero prefiero más dulces que salados.', 'value': 'D' }];

    this.tolerancia = [{ 'text': 'Alta en estrés prolongado e intenso.', 'value': 'A' },
    { 'text': 'Baja, no tolero el estrés.', 'value': 'B' },
    { 'text': 'Buena pero decae con estrés muy intenso o prolongado.', 'value': 'C' },
    { 'text': 'Buena pero decae generando ansiedad.', 'value': 'D' }];

  }
  createMyForm() {
    return this.formBuilder.group({
      apetito: ['', Validators.compose([Validators.required])],
      sueno: ['', Validators.compose([Validators.required])],
      digestion: ['', Validators.compose([Validators.required])],
      episodio: ['', Validators.compose([Validators.required])],
      ojos: ['', Validators.compose([Validators.required])],
      saliva: ['', Validators.compose([Validators.required])],
      estres: ['', Validators.compose([Validators.required])],
      alimento: ['', Validators.compose([Validators.required])],
      snack: ['', Validators.compose([Validators.required])],
      tolerancia: ['', Validators.compose([Validators.required])]
    });
  }

  onSubmit({ value, valid }: { value: Metabolico, valid: boolean }) {
    this.submitted = true;
    this.submittedModel = value;
    this.data.metabolico = this.submittedModel;
    this.router.navigate(['somatotipo']);
  }
}
