import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, } from '@angular/router';
import { DataService } from '../data.service';
import { Bienvenida } from '../shared/bienvenida';
@Component({
  selector: 'app-bienvenida',
  templateUrl: './bienvenida.component.html',
  styleUrls: ['./bienvenida.component.css']
})

export class BienvenidaComponent implements OnInit {
  // @Output('texto1') enviar = new EventEmitter<string>();
  sexo: string[];
  baseForm: FormGroup;
  submitted = false;
  model: Bienvenida;
  submittedModel: Bienvenida;
  resultado: string;
  resultado_text: string;
  resultado2_text: string;
  resultado3_text: string;
  condition = false;
  hello = false;
  constructor(private formBuilder: FormBuilder, public router: Router, private data: DataService) {
    this.baseForm = this.createMyForm();
  }

  ngOnInit() {
    this.sexo = ['Hombre', 'Mujer'];
  }
  createMyForm() {
    return this.formBuilder.group({
      sexo: ['', Validators.compose([Validators.required])],
      talla: ['', Validators.compose([Validators.pattern('^[1-2](\\.[0-9]{1,2})?$'), Validators.required])],
      peso: ['', Validators.compose([Validators.pattern('^([0-9]{1,3})(\\.[0-9]{1,2})?$'), Validators.required])],
      edad: ['', Validators.compose([Validators.pattern('^[0-9]{2}$'), Validators.required])]

    });
  }
  onSubmit({ value, valid }: { value: Bienvenida, valid: boolean }) {
    this.resultado_text = '¡Felicidades!';
    this.resultado2_text = 'Su Índice de Masa ';
    this.resultado3_text = ' Corporal es: ';
    this.resultado = (parseFloat(value.peso) / (Math.pow(parseFloat(value.talla), 2))).toFixed(3);
    value.peso = value.peso.toString().replace(',', '.');
    value.talla = value.talla.toString().replace(',', '.');
    this.condition = true;
    this.submittedModel = value;
    this.data.base = this.submittedModel;
  }
  continuar() {
    this.submitted = true;
    this.router.navigate(['acumulacion']);
  }

}
