import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Final } from '../shared/final';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient, HttpHeaders, HttpClientModule } from '@angular/common/http';
import { Router, NavigationEnd } from '@angular/router';
@Component({
  selector: 'app-final',
  templateUrl: './final.component.html',
  styleUrls: ['./final.component.css']
})
export class FinalComponent implements OnInit {
  finalForm: FormGroup;
  model: Final;
  submittedModel: Final;
  submitted = false;
  nombre: object;
  correo: object;
  resultado: string;
  constructor(private http: HttpClient, private formBuilder: FormBuilder, private data: DataService, public router: Router) {
    this.finalForm = this.createMyForm();
  }


  ngOnInit() {
    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0);
  });

  }
  createMyForm() {
    return this.formBuilder.group({
      nombre: ['', Validators.compose([Validators.pattern('^[aA-zZ áéíóúÁÉÍÓÚñÑ]{2,35}$'), Validators.required])],
      // tslint:disable-next-line:max-line-length
      correo: ['', Validators.compose([Validators.pattern('^[A-Za-z0-9](([_\.\-]?[a-zA-Z0-9]+)*)@([A-Za-z0-9]+)(([\.\-]?[a-zA-Z0-9]+)*)\.([A-Za-z]{2,})$'), Validators.required])]

    });
  }

  onSubmit({ value, valid }: { value: Final, valid: boolean }) {
    this.submitted = true;
    this.submittedModel = value;
    this.data.final = this.submittedModel;
     /*console.log(this.data.base);
    console.log(this.data.acumulacion);
    console.log(this.data.metabolico);
    console.log(this.data.somatotipo);*/

    const merged = Object.assign(this.data.base, this.data.acumulacion, this.data.metabolico, this.data.somatotipo, this.data.final);
    console.log(merged);
    this.resultado = 'La información ha sido enviada correctamente, pronto nos contactaremos contigo.';
    const header = new HttpHeaders({ 'Content-Type': 'application/json' });

    const req = this.http.post('https://dragonservice.herokuapp.com/tests', JSON.stringify(merged), { headers: header })
      .subscribe(
        res => {
          console.log(res);
        },
        err => {
          console.log(err);
        }
      );
  }
}


